<template>
  <div class="acclist">
    <!-- 卡片 -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>账户列表</span>
      </div>
      <el-table
        ref="acclistTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="selectionChange"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="account" label="账号" width="120">
          <template slot-scope="scope">{{ scope.row.account }}</template>
        </el-table-column>
        <el-table-column
          prop="userGroup"
          label="用户组"
          width="120"
        >
          <template slot-scope="scope">{{ scope.row.userGroup }}</template>
        </el-table-column>
        <el-table-column
          prop="ctime"
          label="创建时间"
          width="200"
        >
          <template slot-scope="scope">{{ scope.row.ctime }}</template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" @click="eidit(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" @click="del(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="sizeChange"
          @current-change="currentChange"
          :current-page="currentPage"
          :page-sizes="[1, 3, 5, 10]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
      <div style="margin-top: 20px">
        <el-button type="danger" @click="delAll">批量删除</el-button>
        <el-button type="primary" @click="cancel()">取消选择</el-button>
      </div>
      <!-- 弹窗 -->
      <el-dialog title="修改账户" :visible.sync="dialogFormVisible">
        <el-form :model="eiditForm">
          <el-form-item label="账号" label-width="120px">
            <el-input v-model="eiditForm.account" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="用户组" label-width="120px">
            <el-select v-model="eiditForm.userGroup" placeholder="请选择用户组">
              <el-option label="超级管理员" value="超级管理员"></el-option>
              <el-option label="普通管理员" value="普通管理员"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="ensure">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
//引入api
import {
  api_accList,
  api_accDel,
  api_accAllDel,
  api_accEidit,
} from "@/api/acc.js";
//引入时间格式修改
import moment from "moment"
export default {
  data() {
    return {
      eiditForm: {
        id: "",
        account: "",
        userGroup: "",
      },
      rules: {
        account: { required: true, message: "用户名不能为空", trigger: "blur" },
        userGroup: {
          required: true,
          message: "用户组不能为空",
          trigger: "blur",
        },
      },
      //编辑弹窗
      dialogFormVisible: false,
      tableData: [
        // {
        //   id: 1,
        //   username: "王小虎",
        //   group: "超级管理员",
        //   date: "2020-10-25",
        // },
      ],
      multipleSelection: [],
      currentPage: 1,
      pageSize: 3,
      total: 5,
    };
  },
  methods: {
    //封装获取数据库列表
    async getAccList() {
      let { total, data } = await api_accList({
        pageSize: this.pageSize,
        currentPage: this.currentPage,
      });

      //边界值判断
      if (!data.length && this.currentPage != 1) {
        this.currentPage -= 1;
        this.getAccList();
      }
      // // 再复制之前更改时间格式
      data.forEach(item=>{
        item.ctime = moment(item.ctime).format("YYY-MM-DD HH:mm:ss")
      });
      //渲染到页面
      this.tableData = data; //用户列表
      this.total = total; //总条数
    },
    //点击弹出框确定按钮数据改变
    async ensure() {
      this.dialogFormVisible = false;
      let { code, msg } = await api_accEidit(this.eiditForm);
      
    },
    //编辑账号回填
    eidit(row) {
      (this.dialogFormVisible = true), (this.eiditForm = { ...row });
    },
    //表单单项删除
    del(row) {
      this.$confirm("此操作将永久删除该列表, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          let { code, msg } = await api_accDel({ id: row.id });
          this.getAccList();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    //批量删除
    delAll() {
      this.$confirm("此操作将永久删除该列表, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(async () => {
          let { code, msg } = await api_accAllDel({
            ids: JSON.stringify(this.ids),
          });
          this.getAccList(); //重绘
          
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    //取消选择
    cancel(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.acclistTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.acclistTable.clearSelection();
      }
    },
    //多选框变化触发事件
    selectionChange(rows) {
      // this.multipleSelection = rows;
      //取出数组中的Id挂载在原型上
      this.ids = rows.map((item) => item.id);
    },

    //当页码变化触发
    sizeChange(val) {
      this.pageSize = val;
      this.getAccList();
    },
    // 当前页面变化触发
    currentChange(val) {
      this.currentPage = val;
      this.getAccList();
    },
  },
  //从创建后生命周期调用
  created() {
    this.getAccList();
  },
};
</script>

<style lang="less" scoped>
.acclist {
  /deep/.el-dialog {
    width: 500px;
    /deep/ .el-input__inner {
      width: 271px;
    }
  }

  .el-table {
    margin-bottom: 10px;
  }
  .pagination {
    padding-left: 10px;
  }
  /deep/ h3 {
    border-bottom: 1px solid #eff1f7;
    padding: 10px;
  }
}
</style>